// Main files
// Do not delete these
// You can configure which are included
// within the 'optional-styles' mixin below
@import 'partials/_fonts.scss';
@import 'partials/_variables.scss';
@import 'partials/_type.scss';
@import 'partials/_forms.scss';
@import 'partials/_description-lists.scss';
@import 'partials/_button.scss';
@import 'partials/_call-to-action.scss';
@import 'partials/_details.scss';
@import 'partials/_stack.scss';
@import 'partials/_cluster.scss';
@import 'partials/_grid.scss';
@import 'partials/_sidebar.scss';
@import 'partials/_breadcrumbs.scss';
@import 'partials/_card.scss';
@import 'partials/_candidate.scss';
@import 'partials/_table.scss';
@import 'partials/_language.scss';
@import 'partials/_header.scss';
@import 'partials/_subnav.scss';
@import 'partials/_footer.scss';
@import 'partials/_radio.scss';
@import 'partials/_checkbox.scss';
@import 'partials/_select.scss';
@import 'partials/_filter.scss';
@import 'partials/_status.scss';
@import 'partials/_codeblock.scss';
@import 'partials/_dark.scss';
@import 'partials/_page.scss';
@import 'partials/_utilities.scss';
@import 'partials/_logo.scss';

// Whether to scope basic styles to the .ds-scope class
// For `true`, styles only apply inside
// containers with `class="ds-scope"`
$scope: false;

// Comment out the component styles
// you don’t need
@mixin optional-styles {
  @include description-lists;
  @include button;
  @include cta;
  @include cluster;
  @include grid;
  @include sidebar;
  @include details;
  @include card;
  @include candidate;
  @include breadcrumbs;
  @include table;
  @include language;
  @include language;
  @include header;
  @include subnav;
  @include footer;
  @include radio;
  @include checkbox;
  @include select;
  @include filter;
  @include status;
  @include codeblock;
  @include logo;
}

@if $scope {
  .ds-scope {
    font-size: clamp(#{$s1}, 2vw, #{$s2});
    @include fontMain;
    @include type;
    @include forms;
    @include stack;
    @include optional-styles;
    @include dark;
    @include page;
    @include utilities;
  }

  .ds-scope *,
  .ds-scope *::before,
  .ds-scope *::after {
    box-sizing: border-box;
  }
} @else {
  :root {
    font-size: clamp(#{$s1}, 2vw, #{$s2});
    @include fontMain;
  }

  :root *,
  :root *::before,
  :root *::after {
    box-sizing: border-box;
  }

  body {
    margin: 0;
  }

  @include type;
  @include forms;
  @include stack;
  @include optional-styles;
  @include dark;
  @include page;
  @include utilities;
}


$DarkGray: rgba(220, 220, 220, 1);

table {
  border-collapse: collapse;
  width:100%;
}
tbody tr {
  border-left: 8px solid $DarkGray;
  &.status-200 {
    border-left-color: $greenForWhite;
  }
  &.status-404 {
    border-left-color: red;

  }
}


.run-graph {
  display: table;
}
.run-graph > a {
  width: 15px;
  height:30px;
  background-color: $DarkGray;
  vertical-align: bottom;
  padding:0 1px;
  display: table-cell;
}

.run-graph a .duration {
  border-bottom: 4px darkgray solid;
  height:15px;
}
.run-graph a.status-1 .duration {
  background-color: red;
  border-bottom-color: red;
  border-top:1px solid #666;
}
.run-graph a.status-0 .duration {
  background-color: $greenForWhite;
  border-bottom-color: $greenForWhite;
  border-top:1px solid #666;
}

pre {
  font-size:0.9em;
  font-family: Monospaced, "Ubuntu Mono", monospace;
}
